import { Component, OnDestroy, OnInit, TemplateRef, ViewChild, ViewEncapsulation } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { Router, ActivatedRoute } from '@angular/router';
import { MatDialog, MatDialogRef, MatPaginator, MatSort } from '@angular/material';
import { DataSource } from '@angular/cdk/collections';
import { Subscription } from 'rxjs/Subscription';
import { Observable } from 'rxjs/Observable';
import { MatTableDataSource } from '@angular/material';

import { fuseAnimations } from '@fuse/animations';
import { FuseConfirmDialogComponent } from '@fuse/components/confirm-dialog/confirm-dialog.component';
import { AccountDetailsDialogComponent } from '../account-details-dialog/account-details-dialog.component';

import { AccountsService } from '../accounts.service';
import { Account } from '../account.model';

import { merge, map, take } from 'rxjs/operators';

@Component({
    selector     : 'fuse-account-table',
    templateUrl  : './account-table.component.html',
    styleUrls    : ['./account-table.component.scss'],
    encapsulation: ViewEncapsulation.None,
    animations   : fuseAnimations
})
export class FuseAccountTableComponent implements OnInit, OnDestroy
{
    @ViewChild('dialogContent') dialogContent: TemplateRef<any>;
    @ViewChild(MatPaginator) paginator: MatPaginator;
    @ViewChild(MatSort) sort: MatSort;

    accounts: any;
    dataSource: AccountsDataSource | null;
    displayedColumns = ['checkbox', 'accountName', 'partnerType', 'shipState', 'phone', 'salesRep', 'dateCreated', 'buttons'];
    selectedCount: any[];
    checkboxes: {};
    // editing = false;
    totalCount = 0;
    loading = false;

    onAccountsChangedSubscription: Subscription;
    onSelectedAccountsChangedSubscription: Subscription;

    confirmDialogRef: MatDialogRef<FuseConfirmDialogComponent>;
    dialogRef: MatDialogRef<AccountDetailsDialogComponent>;

    constructor(
        private accountsService: AccountsService,
        private router: Router,
        private route: ActivatedRoute,
        public dialog: MatDialog
    )
    {
        this.onAccountsChangedSubscription =
            this.accountsService.onAccountsChanged.subscribe(accounts => {
                this.accounts = accounts;

                this.checkboxes = {};
                accounts.map(account => {
                    this.checkboxes[account.id] = false;
                });
            });

        this.onSelectedAccountsChangedSubscription =
            this.accountsService.onSelectedAccountsChanged.subscribe(selectedAccounts => {
                for ( const id in this.checkboxes ) {
                    if ( !this.checkboxes.hasOwnProperty(id) ) {
                        continue;
                    }

                    this.checkboxes[id] = selectedAccounts.includes(id);
                }
                this.selectedCount = selectedAccounts.length;
            });
    }

    ngOnInit()
    {
        this.dataSource = new AccountsDataSource(this.accountsService);

        this.route.data.subscribe(data => {
            this.totalCount = data.data[0];
        });
    }

    ngOnDestroy()
    {
        this.onAccountsChangedSubscription.unsubscribe();
        this.onSelectedAccountsChangedSubscription.unsubscribe();
    }

    /**
     * Delete Account
     */
    deleteAccount(account)
    {
        this.confirmDialogRef = this.dialog.open(FuseConfirmDialogComponent, {
            disableClose: false
        });

        this.confirmDialogRef.componentInstance.confirmMessage = 'Are you sure you want to delete?';

        this.confirmDialogRef.afterClosed().subscribe(result => {
            if ( result )
            {
                this.accountsService.deleteAccount(account);
            }
            this.confirmDialogRef = null;
        });

    }

    onSelectedChange(accountId)
    {
        this.accountsService.toggleSelectedAccount(accountId);
    }

    editAccount(account)
    {
        // if (!this.editing) {
        //     this.editing = true;

        //     this.accountsService.getAccountDetail(account.id)
        //         .then((acc: Account) => {
        //             this.dialogRef = this.dialog.open(AccountDetailsDialogComponent, {
        //                 panelClass: 'account-details-dialog',
        //                 data      : {
        //                     action: 'edit',
        //                     account: acc
        //                 }
        //             });
        //         });
        // }
    }

    toggleAll(ev) {
        if (ev) {
            this.accountsService.toggleSelectAll();
        }
    }

    paginate(ev) {
        if (this.loading) {
            return;
        }

        this.loading = true;

        this.accountsService
            .getAccountList(ev.pageIndex, ev.pageSize)
            .then(() => this.loading = false)
            .catch(() => this.loading = false);
    }

    sortChange(ev) {
        if (this.loading) {
            return;
        }

        this.loading = true;
        this.accountsService.getAccountList(
            this.paginator.pageIndex,
            this.paginator.pageSize,
            ev.active,
            ev.direction
        ).then(() => this.loading = false)
         .catch(() => this.loading = false);
    }
}

export class AccountsDataSource extends DataSource<any>
{
    constructor(private accountsService: AccountsService) {
        super();
    }

    connect(): Observable<Account[]>
    {
        return this.accountsService.onAccountsChanged;
    }

    disconnect() { }
}



