import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { ActivatedRouteSnapshot, Resolve, RouterStateSnapshot } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { BehaviorSubject } from 'rxjs/BehaviorSubject';
import { Subject } from 'rxjs/Subject';
import { MatSnackBar } from '@angular/material';
import { map } from 'rxjs/operators';

import { FuseUtils } from '@fuse/utils';
import { searchFields } from './constants';
import { getSearchTerm } from './utils';

import { Account } from './account.model';
import { ApiService } from '../../core/services/api.service';

@Injectable()
export class AccountsService implements Resolve<any>
{
    onAccountsChanged: BehaviorSubject<any> = new BehaviorSubject([]);
    onTotalCountChanged: BehaviorSubject<number> = new BehaviorSubject(0);
    onSelectedAccountsChanged: BehaviorSubject<any> = new BehaviorSubject([]);
    onSearchTextChanged: Subject<any> = new Subject();

    accounts: Account[] = [];
    selectedAccounts: string[] = [];
    totalCount = 0;

    searchText: string;

    constructor(
        private http: HttpClient,
        private api: ApiService,
        private snackBar: MatSnackBar
    ) {
        this.onSearchTextChanged.subscribe(searchText => {
            this.searchText = searchText;

            this.getTotalCount().then(console.log);
            this.getAccountList().then(console.log);
        });
    }

    resolve(route: ActivatedRouteSnapshot, state: RouterStateSnapshot): Promise<any> {
        return Promise.all([
            this.getTotalCount(),
            this.getAccountList()
        ]);
    }

    getTotalCount() {
        return this.api.post('/content/get-account-count', {
            term: getSearchTerm(searchFields, this.searchText)
        })
        .toPromise()
        .then((res: any) => {
            this.totalCount = res.count;
            return Promise.resolve(this.totalCount);
        });
    }

    getAccountList(offset=0, limit=10, order='', orient='asc') {
        const data = {
            offset,
            limit,
            order,
            orient,
            term: getSearchTerm(searchFields, this.searchText)
         };

        return this.api.post('/content/get-account-list', data)
            .toPromise()
            .then((list: Account[]) => {
                this.accounts = list;
                this.onAccountsChanged.next(list);
                
                return list;
            });
    }

    getAccountDetail(id) {
        return this.api.post('/content/get-account-details', { id }).toPromise();
    }

    toggleSelectedAccount(id) {
        if ( this.selectedAccounts.length > 0 ) {
            const index = this.selectedAccounts.indexOf(id);

            if ( index !== -1 ) {
                this.selectedAccounts.splice(index, 1);
                this.onSelectedAccountsChanged.next(this.selectedAccounts);

                return;
            }
        }

        this.selectedAccounts.push(id);
        this.onSelectedAccountsChanged.next(this.selectedAccounts);
    }

    toggleSelectAll()
    {
        if ( this.selectedAccounts.length === this.accounts.length) {
            this.deselectAccounts();
        } else {
            this.selectAccounts();
        }
    }

    selectAccounts() {
        this.selectedAccounts = this.accounts.map(acc => acc.id);
        this.onSelectedAccountsChanged.next(this.selectedAccounts);
    }

    deselectAccounts() {
        this.selectedAccounts = [];
        this.onSelectedAccountsChanged.next(this.selectedAccounts);
    }

    addAccount(account) {
        delete account.id;
        
        return this.api.post('/content/create-account', account)
            .map((response: CreateAccountResponseType) => {
                if (response.status === 'success') {
                    this.accounts.push(response.extra);
                    this.onAccountsChanged.next(this.accounts);
                } else {
                    this.snackBar.open(response.msg, 'Dismiss', {
                        verticalPosition: 'top',
                        duration: 3000
                    });
                }

                return response;
            });
    }

    updateAccount(account) { }

    deleteAccount(account) { }

    deleteSelectedAccounts() { }
}

interface CreateAccountResponseType {
    status: string;
    msg: string;
    extra?: Account;
}


