import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { CdkTableModule } from '@angular/cdk/table';
import { ChartsModule } from 'ng2-charts';
import { NgxChartsModule } from '@swimlane/ngx-charts';
import {
    MatButtonModule,
    MatCheckboxModule,
    MatNativeDateModule,
    MatDatepickerModule,
    MatFormFieldModule,
    MatIconModule,
    MatInputModule,
    MatMenuModule,
    MatPaginatorModule,
    MatProgressSpinnerModule,
    MatRippleModule,
    MatSelectModule,
    MatSidenavModule,
    MatSortModule,
    MatTabsModule,
    MatTableModule,
    MatToolbarModule
} from '@angular/material';

import { FuseSharedModule } from '@fuse/shared.module';
import { FuseConfirmDialogModule } from '@fuse/components';
import { SharedModule } from '../../shared/shared.module';

import { FuseAccountsMainSidenavComponent } from './sidenavs/main/main.component';
import { AccountsService } from './accounts.service';
import { FuseAccountListComponent } from './account-list/account-list.component';
import { FuseAccountTableComponent } from './account-table/account-table.component';
import { FuseAccountsSelectedBarComponent } from './selected-bar/selected-bar.component';
import { AccountFormComponent } from './account-form/account-form.component';
import { AccountDetailsDialogComponent } from './account-details-dialog/account-details-dialog.component';

const routes: Routes = [
    {
        path: '**',
        component: FuseAccountListComponent,
        resolve  : {
            data: AccountsService
        }
    }
];

@NgModule({
    declarations   : [
        FuseAccountListComponent,
        FuseAccountTableComponent,
        FuseAccountsSelectedBarComponent,
        FuseAccountsMainSidenavComponent,
        AccountFormComponent,
        AccountDetailsDialogComponent
    ],
    imports        : [
        RouterModule.forChild(routes),
        CdkTableModule,

        ChartsModule,
        NgxChartsModule,

        MatButtonModule,
        MatCheckboxModule,
        MatNativeDateModule,
        MatDatepickerModule,
        MatFormFieldModule,
        MatIconModule,
        MatInputModule,
        MatMenuModule,
        MatPaginatorModule,
        MatProgressSpinnerModule,
        MatRippleModule,
        MatSelectModule,
        MatSidenavModule,
        MatSortModule,
        MatTabsModule,
        MatTableModule,
        MatToolbarModule,

        FuseSharedModule,
        FuseConfirmDialogModule,
        SharedModule
    ],
    providers      : [
        AccountsService
    ],
    entryComponents: [
        AccountDetailsDialogComponent
    ]
})
export class FuseAccountsModule
{
}



