import { Component, OnDestroy, OnInit, ViewEncapsulation } from '@angular/core';
import { FormControl, FormGroup } from '@angular/forms';
import 'rxjs/add/operator/debounceTime';
import 'rxjs/add/operator/distinctUntilChanged';
import { Subscription } from 'rxjs/Subscription';
import { MatDialog, MatDialogRef } from '@angular/material';

import { fuseAnimations } from '@fuse/animations';
import { FuseConfirmDialogComponent } from '@fuse/components/confirm-dialog/confirm-dialog.component';

import { AccountsService } from '../accounts.service';
import { AccountDetailsDialogComponent } from '../account-details-dialog/account-details-dialog.component';

@Component({
    selector     : 'fuse-account-list',
    templateUrl  : './account-list.component.html',
    styleUrls    : ['./account-list.component.scss'],
    encapsulation: ViewEncapsulation.None,
    animations   : fuseAnimations
})
export class FuseAccountListComponent implements OnInit, OnDestroy
{
    selectedCount: boolean;
    searchInput: FormControl;
    onSelectedAccountsChangedSubscription: Subscription;

    confirmDialogRef: MatDialogRef<FuseConfirmDialogComponent>;
    dialogRef: MatDialogRef<AccountDetailsDialogComponent>;

    adding = false;

    constructor(
        private accountsService: AccountsService,
        public dialog: MatDialog
    )
    {
        this.searchInput = new FormControl('');
    }

    ngOnInit()
    {
        this.onSelectedAccountsChangedSubscription =
            this.accountsService.onSelectedAccountsChanged
                .subscribe(selectedAccounts => {
                    this.selectedCount = selectedAccounts.length;
                });

        this.searchInput.valueChanges
            .debounceTime(300)
            .distinctUntilChanged()
            .subscribe(searchText => {
                this.accountsService.onSearchTextChanged.next(searchText);
            });
    }

    ngOnDestroy()
    {
        this.onSelectedAccountsChangedSubscription.unsubscribe();
    }

    newAccount() {
        if (!this.adding) {
            this.adding = false;

            this.dialogRef = this.dialog.open(AccountDetailsDialogComponent, {
                panelClass: 'account-details-dialog',
                data      : {
                    action: 'new'
                }
            });
        }
    }

    deleteSelectedAccounts()
    {
        this.confirmDialogRef = this.dialog.open(FuseConfirmDialogComponent, {
            disableClose: false
        });

        this.confirmDialogRef.componentInstance.confirmMessage = 'Are you sure you want to delete all selected contacts?';

        this.confirmDialogRef.afterClosed().subscribe(result => {
            if ( result )
            {
                this.accountsService.deleteSelectedAccounts();
            }
            this.confirmDialogRef = null;
        });
    }
}



