import { FuseUtils } from '@fuse/utils';
import { timezones } from './constants';

export class Account {
    id: string = '';
    accountName: string = '';
    parentAccount: string = '';
    rating: string = '';
    partnerType: string = '';
    timeZone: string = '';
    industry: string = '';
    accountExtension: string = '';
    website: string = '';
    shipAddress1: string = '';
    shipAddress2: string = '';
    shipCity: string = '';
    shipState: string = '';
    shipPostalcode: string = '';
    shipCountry: string = '';
    salesRep: string = '';
    phone: string = '';
    fax: string = '';
    leadSource: string = '';
    priorSalesRep: string = '';
    accountType: string = '';
    shippingAccountNo: string = '';
    shippingAccountType: string = '';
    billAddress1: string = '';
    billAddress2: string = '';
    billCity: string = '';
    billState: string = '';
    billPostalcode: string = '';
    billCountry: string = '';
    generalInfo: string = '';
    creditTerms: string = '';
    taxStatus: string = '';
    taxExemptNo: string = '';
    taxExempt: string = '';
    vipDiscountsForOrders: string = '';
    ytdSales: string = '';
    lastYearSales: string = '';
    highCreditLimit: string = '';
    defaultTaxRate: string = '';
    webstoreUrl: string = '';

    constructor(account: Object = {}) {
        Object.assign(this, account);
    }
}



